import React, { useState, useEffect } from "react"
import axios from "axios"
import LinkCard from "../components/LinkCards"
import { GridList, GridListTile, makeStyles, Typography } from "@material-ui/core"

const useStyles = makeStyles(theme => ({
  gridListHolder: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',

  },
  gridList: {
    flexWrap: "nowrap",
    padding: theme.spacing(0,2,1,2),
  },
  discoverHeader: {
    padding: theme.spacing(0,2)
  },
  containerWrapper: {
    margin: theme.spacing(2,0)
  }
}))

const DiscoverProvider = ({ title, query }) => {

  const classes = useStyles()
  /* const newsProviderApi = "https://newsapi.org/v2/" */
  const newsProviderApi = "https://api.currentsapi.services/v1/"
  /* const apiKey = "&apiKey=28bc2e36c58a4e4cbe1cc35c93fd54fa" */
  const apiKey = "apiKey=-V2Sp7WAWXtgTlbqlnRQiPwCaBnSviDzx2nzkIOmxAs1QHEl"

  const useNews = () => {
    const [news, setNews] = useState([])
    useEffect(() => {
      const unsubscribe = axios.get(newsProviderApi + query + apiKey)
        .then(res => {
          console.log({res})
          setNews(res.data.news)
        }).catch(err => {
          console.log({err})
        })
      return () => unsubscribe
      // TO-DO: PROPER CLEANUP
    }, [])

    return news
  }

  const articles = useNews()
  const articleCards = articles.map(article => (
    <GridListTile key={article.url}>
      <LinkCard
          title={article.title}
          url={article.url}
          img={article.image}
        />
    </GridListTile>
  ))

  return (
    <div className={classes.containerWrapper} >
      <div className={classes.discoverHeader}>
        <Typography variant="h6" gutterBottom>
          {title}
        </Typography>
      </div>
      <div className={classes.gridListHolder} >
        <GridList className={classes.gridList} cols={1.5} cellHeight={"auto"} spacing={8}>
          {articleCards}
        </GridList>
      </div>
    </div>
  )
}

export default DiscoverProvider